/******************\
	 #CONTENT__SKILLS--HTML5
	\******************/

.html__percent {
  position: relative;
  width: 150px;
}

.html__svg {
  position: relative;
  width: 150px;
  height: 150px;
  z-index: 1000;
}

.html__circle {
  width: 100%;
  height: 100%;
  fill: none;
  stroke: #b2bec3;
  stroke-width: 9;
  stroke-linecap: square;
  transform: translate(5px, 5px);
}

.html__circle:nth-child(2) {
  stroke-dasharray: 440;
  stroke-dashoffset: 440;
}

.html__circle:nth-child(2) {
  stroke-dashoffset: calc(440 - (440 * 50) / 100);
  stroke: #636e72;
}

.html__img {
  position: absolute;
  width: 60px;
  top: 35px;
  left: 45px;
  z-index: 1001;
}

.html__nom {
  margin-top: 10px;
  text-align: center;
}
.html__nom--h2 {
  font-family: "Bebas Neue", cursive;
  font-size: 30px;
}

