.App {
  margin: 0;
  padding: 0;
  text-align: center;
  display: grid;
  grid-template-columns: 25% 75%;
  grid-template-rows: 100%;
}

/* Side Bar Section */

.side-bar {
  margin: 0 auto;
  height: 100%;
  background: #2d3436;
}

.side-bar-avatar {
  margin: 50px auto;
  width: 200px;
  height: 200px;
}

.avatar-img {
  border-radius: 50%;
  width: 100%;
}

.sidebar__subtitle {
  margin: 40px 50px 0 50px;
  padding: 0 0 15px 0;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  color: white;
  border-bottom: 2px solid white;
}

.text {
  margin: 10px 50px 0 50px;
  padding: 5px 0 5px 0;
  color: white;
}

.sideBar__icon {
  display: inline-block;
  margin: 10px 10px 0 10px;
}

.icon {
  width: 30px;
}

.text--h2 {
  font-weight: bold;
  padding: 0 0 5px 0;
}

/* Content Section */

.content {
  width: 100%;
  background: white;
}

.content-header {
  width: 100%;
  padding: 100px 50px 50px 50px;
  text-align: left;
}

.header-h1 {
  font-size: 4rem;
  font-weight: 700;
}

.grid {
  text-align: left;
  width: 80%;
}

/* Content Subtitle */

.content__subtitle {
  width: 100%;
  margin: 10px 50px;
  display: grid;
  grid-template-columns: 60px 1fr;
  grid-template-rows: 50px;
  align-items: center;
}

.subtitle__image {
  position: relative;
  width: 50px;
  height: 50px;
  background-color: #2d3436;
  border-radius: 50%;
}

.subtitle__img {
  position: absolute;
  left: 3px;
  width: 45px;
  height: 45px;
}

.subtitle__h2 {
  font-size: 25px;
  font-weight: 600;
  color: #2d3436;
  text-align: left;
  text-transform: uppercase;
}

/* Content Internship */
.content__internship {
  width: 100%;
  padding: 20px 50px;
  text-align: left;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 20px;
}

.internship__data {
  font-weight: bold;
}

/* Content-Timeline */
.content__timeline {
  position: relative;
  width: 100%;
  height: 200px;
  padding: 0 20px;
}

.timeline__grid {
  position: absolute;
  width: 90%;
  height: 5px;
  background: #2d3436;
  top: 50%;
  left: 5%;
}

.timeline__dot {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #2d3436;
  border: 1px solid #2d3436;
}

.timeline__dot:hover {
  background: white;
  width: 10px;
  height: 10px;
}

.dot--1 {
  top: 49%;
  left: 18%;
}

.dot--2 {
  top: 49%;
  left: 39%;
}

.dot--3 {
  top: 49%;
  left: 62%;
}

.dot--4 {
  top: 49%;
  left: 81%;
}

.timeline__text {
  position: absolute;
  text-align: center;
  color: #2d3436;
}

.text--1 {
  top: 55%;
  left: 8.5%;
}
.text--2 {
  top: 55%;
  left: 33.5%;
}
.text--3 {
  top: 55%;
  left: 56.5%;
}
.text--4 {
  top: 55%;
  left: 75%;
}

/* Content Skill */

.content__skills {
  margin: 40px 0px 30px 29px;
  padding: 0 0 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1;
  justify-content: center;
  align-content: center;
  align-items: center;
  justify-items: center;
}

/* Content-Footer */
.content__footer {
  padding: 20px 50px;
  text-align: left;
}

